import React from 'react';
import { navigate } from 'gatsby';
import { PostList, PostListItem, PostItemHeader, PostTag, PostIcon } from './style';

const PostItem = ({ post, title }) => {
 return (
  <PostList>
   <PostListItem itemScope itemType="http://schema.org/Article">
    <PostIcon
     onClick={() => {
      navigate(post.fields.slug);
     }}
    >
     {post.frontmatter.icon}
    </PostIcon>
    <header
     onClick={() => {
      navigate(post.fields.slug);
     }}
    >
     <span itemProp="headline">{title}</span>
    </header>
    <PostItemHeader>
     {post.frontmatter.tags.map((tag) => (
      <PostTag
       key={tag}
       onClick={() => {
        navigate(`tags/${tag}`);
       }}
      >
       {tag}
      </PostTag>
     ))}
    </PostItemHeader>
    <section
     onClick={() => {
      navigate(post.fields.slug);
     }}
    >
     <p>{post.excerpt}</p>
    </section>
    <small>{post.frontmatter.date}</small>
   </PostListItem>
  </PostList>
 );
};

export default PostItem;
